import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Produtcs from "../views/Products.vue";
import Services from "../views/Services.vue";
import Rep from "../views/Rep.vue";
import Clients from "../views/Clients.vue";
import Pricing from "../views/Contact.vue";
import Solutions from "../views/Solutions.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/products",
    name: "Products",
    component: Produtcs
  },
  {
    path: "/services",
    name: "Services",
    component: Services
  },
  {
    path: "/Rep",
    name: "Rep",
    component: Rep
  },
  {
    path: "/Clients",
    name: "Clients",
    component: Clients
  },
  {
    path: "/Pricing",
    name: "Pricing",
    component: Pricing
  },
  {
    path: "/Solutions",
    name: "Solutions",
    component: Solutions
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

export default router;
