<template>
  <div>
    <b-container class="padder">
      <div class="divider div-transparent"></div>
      <h1>Servicios</h1>
      <div class="divider div-transparent div-arrow-down"></div>
      <b-row>
        <b-col>
          <div>
            <b-card no-body>
              <b-tabs pills card vertical nav-wrapper-class="w-50">
                <b-tab
                  v-for="item in content"
                  :key="item.id"
                  :title="item.title"
                  ><b-card-text v-html="item.content" align="justify">
                  </b-card-text>
                </b-tab>
              </b-tabs>
            </b-card>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: [],
      contenido: []
    };
  },

  created() {
    this.fetchData();
    this.logs(this.content);
  },

  methods: {
    async fetchData() {
      const res = await fetch("services.json");
      const val = await res.json();
      this.content = val;
    }
  }
};
</script>

